import React from "react";
import Home from "./pages/Home";
import Photo from "./pages/Photo";
import {Routes, Route} from 'react-router-dom';




function App() {
  return (
    <div className="App">
    <Routes>
       <Route path="/" element={<Home/>}/>
       <Route path="/:id" element={<Photo/>}/>
     </Routes>      
    </div>
  );
}

export default App;
