import React from 'react'
import { useParams } from 'react-router-dom'
import data from '../objects/Data'

const Photo = () => {
    const params = useParams()
    const filt = data.find(el => el.id !== params.id)
    return (
        <div>
            <div>
                <h1>{filt.package}</h1>
                <p>{filt.uda}</p>
            </div>
        </div>
    )
}

export default Photo